}

body {
  cursor: url("donut-mause.bcc015ca.png"), pointer;
  scroll-behavior: smooth;
  background-image: url("background-mobile.6c7190bb.png"), linear-gradient(180.11deg, #e5e2e3 10.73%, #fdfefe 38.02%, #e5e2e3 71.11%, #fdfefe 99.91%);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Montserrat, sans-serif;
  position: relative;
}

@media screen and (min-width: 768px) {
  body {
    background-image: url("background-tablet.58cd4d4d.png"), linear-gradient(180.11deg, #e5e2e3 10.73%, #fdfefe 38.02%, #e5e2e3 71.11%, #fdfefe 99.91%);
