.button {
  width: 145px;
  height: 40px;
  color: #fff;
  cursor: url("donut-mause.bcc015ca.png"), pointer;
  background: #f3848c;
  border: none;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.22;
  transition: background-color, all .25s, easy;
  display: flex;
  box-shadow: 10px 15px 25px rgba(232, 183, 183, .45);
}

.button:hover, .button:focus {
  color: #f3848c;
  background-color: #fff;
  border: 1px solid #f3848c;
}

.button:active {
  box-shadow: 10px 15px 25px rgba(232, 183, 183, .45), inset 0 4px 4px rgba(107, 66, 66, .25);
}

