  box-shadow: 0 7px 27px -2px rgba(253, 88, 140, .35);
}

.modal-window {
  z-index: 50;
  visibility: hidden;
  width: 100%;
  height: 100vh;
  background-color: #e5e2e3;
  background-image: url("modal-window__bulbs-mobile.2b0e06bf.png");
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  transition: visibility .25s cubic-bezier(.165, .84, .44, 1);
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  .modal-window {
    background-image: url("modal-window__bulbs-tablet.96f62f69.png");
  }
}

