  justify-content: space-between;
  align-items: flex-start;
  padding-top: 16px;
  padding-bottom: 16px;
  display: flex;
}

.modal-nav {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-nav__item {
  text-align: center;
}

.modal-nav__item:not(:last-child) {
  margin-bottom: 40px;
}

@media screen and (min-width: 768px) {
