  letter-spacing: 0;
  color: #3c322d;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 28px;
  font-weight: 500;
  line-height: 1.2;
  transition: color .25s cubic-bezier(.075, .82, .165, 1);
  display: block;
}

.modal-nav__link:hover, .modal-nav__link:focus {
  color: #fff;
  background: linear-gradient(rgba(248, 110, 103, .63) 0%, rgba(253, 86, 143, .86) 100%);
